@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@600;700&display=swap");
*, *::after, *::before {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.wrapper {
  height: 100vh;
  width: 100%;
  position: relative;
  overflow: hidden;
  background-color: #efd7f5;
}

.overlay {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  position: absolute;
  z-index: 1;
  height: 0px;
  width: 0px;
  background-color: none;
}

.overlay_ai {
  background-color: #E9D1BD;
}

.overlay_pr {
  background-color: #e0c1de;
}

.overlay_ps {
  background-color: #D7DDF5;
}

.overlay_ae {
  background-color: #efd7f5;
}

.main_container {
  z-index: 2;
  perspective: 1000px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 250px;
  height: 50px;
  width: 250px;
  height: 250px;
  overflow: visible;
}

.firstCube {
  z-index: 4;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(0, 0, 0);
  transform-origin: center center;
  transform-style: preserve-3d;
  height: 20px;
  width: 226px;
}
.firstCube .side1 {
  transform-style: preserve-3d;
  position: absolute;
}
.firstCube .fc_top {
  height: 226px;
  width: 226px;
  transform: rotateX(90deg) translateZ(113px);
}
.firstCube .fc_left {
  height: 20px;
  width: 226px;
  transform: rotateY(-90deg) translateZ(113px);
}
.firstCube .fc_front {
  height: 20px;
  width: 226px;
  transform: translateZ(113px);
}
.firstCube .fc_bottom {
  margin-top: 20px;
  height: 226px;
  width: 226px;
  transform: rotateX(90deg) translateZ(113px);
}

.secondCube {
  z-index: 5;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(0, 0, 0);
  transform-origin: center center;
  transform-style: preserve-3d;
  height: 20px;
  width: 226px;
}
.secondCube .side2 {
  transform-style: preserve-3d;
  position: absolute;
  left: 25%;
}
.secondCube .sc_top {
  height: 113px;
  width: 113px;
  background-color: #011B24;
  transform: rotateX(90deg) translateZ(56.5px);
}
.secondCube .sc_left {
  height: 10px;
  width: 113px;
  background-color: #011B24;
  transform: rotateY(-90deg) translateZ(56.5px);
}
.secondCube .sc_front {
  height: 10px;
  width: 113px;
  background-color: #011B24;
  transform: translateZ(56.5px);
}
.secondCube .sc_bottom {
  margin-top: 10px;
  height: 113px;
  width: 113px;
  background-color: #011B24;
  transform: rotateX(90deg) translateZ(56.5px);
}

/* --Common Text Styles */
.textContainer {
  top: 50%;
  left: 50%;
  z-index: 999;
  position: absolute;
  height: 240px;
  width: 240px;
  transform: translate(-50%, -50%);
}
.textContainer h1 {
  opacity: 0;
  position: absolute;
  font-size: 115px;
  height: 100px;
  width: 100px;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
}
.textContainer h1.textMain {
  top: 50%;
  left: 50%;
  text-transform: uppercase;
  transform: translate(-65%, -100%);
}
.textContainer h1.textSecond {
  top: 50%;
  left: 50%;
  text-transform: lowercase;
  transform: translate(0%, -125%);
}

/* Z-Text */
.z-text {
  /* Tip: Apply CSS transforms here */
  /* rotateX() == up/down */
  /* rotateY() == left/right */
  transform: rotateX(40deg) rotateY(12deg) rotateZ(-25deg);
}

/* Individual text */
/* -- AI -- */
.ai {
  color: #EE842C;
}
.ai .z-layer:not(:first-child) {
  color: #A75511;
}

/* -- PR -- */
.pr {
  color: #E777F5;
}
.pr .z-layer:not(:first-child) {
  color: #AE46BE;
}

/* -- PS -- */
.ps {
  color: #2BC3F5;
}
.ps .z-layer:not(:first-child) {
  color: #1785A9;
}

/* -- AE -- */
.ae {
  color: #CB8FF5;
}
.ae .z-layer:not(:first-child) {
  color: #824CAB;
}

/**** KEYFRAMES ****/
@keyframes firstCubeAnimForwards {
  0% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(0, 0, 0);
  }
  20% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1.4, 1.4, 1.4);
  }
  50% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1, 1, 1);
  }
  70% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1.06, 1.06, 1.06);
  }
  80% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1.04, 1.04, 1.04);
  }
  90% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1.02, 1.02, 1.02);
  }
  100% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1, 1, 1);
  }
}
.firstCubeForwardAnim {
  animation: firstCubeAnimForwards 1s linear forwards;
}

@keyframes secondCudeForwardAnim {
  0% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(0, 0, 0);
  }
  5% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(0.5, 0.5, 0.5);
  }
  15% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(0.8, 0.8, 0.8);
  }
  25% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1.4, 1.4, 1.4);
  }
  55% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(2.1, 2.1, 2.1);
  }
  100% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1.7, 1.7, 1.7);
  }
}
.secondCubeForwardAnim {
  animation: secondCudeForwardAnim 0.6s ease-in forwards;
}

@keyframes secondCudeBackwardAnim {
  0% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1.7, 1.7, 1.7);
  }
  45% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(2.1, 2.1, 2.1);
  }
  75% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1.7, 1.7, 1.7);
  }
  90% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1.2, 1.2, 1.2);
  }
  95% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1, 1, 1);
  }
  100% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(0, 0, 0);
  }
}
.secondCubeBackwardAnim {
  animation: secondCudeBackwardAnim 0.8s ease-in forwards;
}

@keyframes firstCubeAnimbackward {
  0% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1, 1, 1);
  }
  10% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1.02, 1.02, 1.02);
  }
  20% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1.04, 1.04, 1.04);
  }
  30% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1.06, 1.06, 1.06);
  }
  50% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1, 1, 1);
  }
  80% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(1.4, 1.4, 1.4);
  }
  100% {
    transform: translate(-50%, -50%) rotateX(-40deg) rotateY(40deg) scale3d(0, 0, 0);
  }
}
.firstCubeBackwardAnim {
  animation: firstCubeAnimbackward 1s linear forwards;
}